.nav {
  width: 310px;
  height: 50px;
  justify-content: space-around;
  align-items: center;
  padding-top: 50px;
  transition: opacity .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.clr {
  height: 30px;
  width: 30px;
  cursor: pointer;
  background-color: #00f;
  border: 3px solid #d6d6d6;
  border-radius: 50%;
  transition: transform .5s;
}

.crl:hover {
  transform: scale(1.2);
}

.clr:first-child {
  background-color: #000;
}

.clr:nth-child(2) {
  background-color: #ef626c;
}

.clr:nth-child(3) {
  background-color: #fdec03;
}

.clr:nth-child(4) {
  background-color: #24d102;
}

.clr:nth-child(5) {
  background-color: red;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #03bb56;
  border: none;
  border-radius: 3px;
  outline: none;
  padding: .6em 1em;
}

.save {
  background-color: #0f65d1;
}

